import React, { useEffect } from 'react'
import AdSense from 'react-adsense';

function Adsub({ ads }) {
  function Add() {
    try {
      return <AdSense.Google
        client={ads.client}
        slot={ads.slot}
        style={{ display: 'block' }}
        format='auto'
        responsive='true'
        layoutKey='-gw-1+2a-9x+5c'
      />
    } catch (error) {
      return (<h1>Error...</h1>)
    }
  }
  return ads ? <Add /> : null
}

export default Adsub